import axios from './../fetch'

// 
export const commandTarget = params => { return axios.get('/admin/platform/command/target', { params: params }) }

// 
export const commandTrancation = params => { return axios.get('/admin/platform/command/trancation', { params: params }) }

// 
export const commandActivity = params => { return axios.get('/admin/platform/command/activity', { params: params }) }

// 
export const commandActivityUser = params => { return axios.get('/admin/platform/command/activityuser', { params: params }) }
// 

export const commandFluxData = params => { return axios.get('/admin/platform/command/fluxdata', { params: params }) }
// 
export const commandPageData = params => { return axios.get('/admin/platform/command/pagedata', { params: params }) }

// 
export const storeMessageSave = params => { return axios.post('/admin/platform/systemmessage/save', params) }
// 
export const storeMessageDelete = params => { return axios.post('/admin/platform/systemmessage/delete', params) }
// 
export const storeMessageAllpage = params => { return axios.get('/admin/platform/systemmessage/page', { params: params }) }
// 
export const storeMessageUnread = params => { return axios.get('/admin/platform/systemmessage/unread', { params: params }) }
// 
export const storeMessageGet = params => { return axios.get('/admin/platform/systemmessage/get', { params: params }) }
// 
export const storeMessageList = params => { return axios.get('/admin/platform/systemmessage/messagepage', { params: params }) }




