import axios from './fetch'
export const dictTypeManager = params => { return axios.get('/admin/platform/dictTypeManager/list', { params: params }) }
export const globalInfo = params => { return axios.get('/admin/platform/globalInfo/get', { params: params }) }
export const getCaptcha = params => { return axios.get('/common/kaptcha', { params: params }) }
// 
export const setIndex = params => { return axios.get('/admin/platform/product/resetIndex', { params: params }) }
// 
export const productinfo = params => { return axios.get('/admin/platform/command/productinfo', { params: params }) }
// 
export const commandChange = params => { return axios.get('/admin/platform/command/change', { params: params }) }




