import Vue from 'vue'
import cmsViewer from './cmsViewer'
import cms from './../../../utils/index'
const MyMsgConstructor = Vue.extend(cmsViewer)
let instance
var MyMsg = function(options) { // 
  const params = {
    show: true
  }
  const el = document.getElementById('cmsViewer')
  if (el) {
    el.parentNode.removeChild(el)
  }
  const type = cms.validateObjcet(options)

  if (type === 'object') {
    instance = new MyMsgConstructor({
      data: Object.assign(params, options)
    })
  }

  //  Vue  el  DOM  vm.$mount() 
  instance.$mount()
  document.body.appendChild(instance.$el)
  return instance
}

export default MyMsg



